export default {
  octocat: `
    position: absolute;
    top: 0;
    right: 15%;

    @media (max-width: 760px) {
      right: 10%;
    }

    @media (max-width: 650px) {
      right: 5%;
    }

    @media (max-width: 580px) {
      top: -50px
    }
  `
};



